import React, {Component} from 'react';
import PropTypes from 'prop-types';

/*import Task from './Task';*/


class Tasks extends Component{

	StyleCompleted() {
        return {
            fontSize: '20px',
            color: this.props.done ? 'gray' : 'black',
            textDecoration: this.props.done ? 'line-through' : 'none'
        }
    }

    render(){
    	{console.log(this.props.task)}
    	if(this.props.task){
	        return (
	        	this.props.task.map((item)=><p style={this.StyleCompleted.call(this, item)}><b>ID:</b> {item.id}  <b>Nombre:</b> {item.title}  
	        	<b>Descripcion: </b>{item.description}  <input id="checkbox" type="checkbox" onChange={this.props.checkDone.bind(item,item.id)}/>
                <button onClick={this.props.deleteTask.bind(item,item.id)}>x</button></p>)
	        );
    	}

    	else{
    		return ""
    	}
	}
}
export default Tasks;



